/* Icons */
@if $slick-font-family == "slick" {
    @font-face {
        font-family: 'Roboto-Light';
    src: local('Roboto'), url(./../../fonts/Roboto-Light.ttf) format('truetype');
    }
}

/* Arrows */

.slick-prev,
.slick-next {
    position: absolute;
    display: block;
    height: 811px;
    width: 64px;
    line-height: 0px;
    font-size: 0px;
    cursor: pointer;
    background:none;
    color: white;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    padding: 0;
    border: none;
    outline: none;
    z-index: 200;
    &:hover, &:focus {
        outline: none;
        background: #0EC261;
        opacity: 0.2;
        &:before {
            opacity: $slick-opacity-on-hover;
        }
    }
    &.slick-disabled:before {
        opacity: $slick-opacity-not-active;
    }
    &:before {
        font-family: $slick-font-family;
        font-size: 20px;
        line-height: 1;
        color: $slick-arrow-color;
        opacity: $slick-opacity-default;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

.slick-prev {
    left: 0px;
    [dir="rtl"] & {
        left: auto;
        right: 0px;
    }
    &:before {
        content: $slick-prev-character;
        [dir="rtl"] & {
            content: $slick-next-character;
        }
    }
}

.slick-next {
    right: 0px;
    [dir="rtl"] & {
        left: 0px;
        right: auto;
    }
    &:before {
        content: $slick-next-character;
        [dir="rtl"] & {
            content: $slick-prev-character;
        }
    }
}

/* Dots */

.slick-dotted.slick-slider {
    margin-bottom: 37px;
    
}

.slick-dots {
    position: absolute;
    bottom: 37px;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;
   z-index:200;
    li {
        position: relative;
        display: inline-block;
        height: 8px;
        width: 8px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;
        button {
            border: 0;
            background: transparent;
            display: block;
            height: 8px;
            width: 8px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            padding: 5px;
            cursor: pointer;
            &:hover, &:focus {
                outline: none;
                &:before {
                    opacity: $slick-opacity-on-hover;                    
                }
            }
            &:before {
                position: absolute;
                top: 0;
                left: 0;
                content: $slick-dot-character;
                width: 8px;
                height: 8px;
                font-family: $slick-font-family;
                font-size: $slick-dot-size;
                line-height: 20px;
                text-align: center;
                color: $slick-dot-color;
                opacity: $slick-opacity-not-active;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }
        }
        &.slick-active button:before {
            color: $slick-dot-color-active;
            opacity: $slick-opacity-default;
        }
    }
    
}
@media screen and (max-width: 1023px) {
    .slick-prev,
    .slick-next {
        width: 32px;        
    }
}
