$mainAccent: #0EC261;
@font-face {
    font-family: 'Roboto-Bold';
    src: local('Roboto'), url(./../../fonts/Roboto-Bold.ttf) format('truetype');
  }
.header{
    display: flex;
    justify-content: flex-end;
}
.header__logo{
position: absolute;
width: 192px;
height: 32px;
left: 128px;
top: 32px;
color: $mainAccent;
filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
white-space: nowrap;
font-family: Roboto-Bold;
font-style: normal;
font-weight: bold;
font-size: 30px;
line-height: 35px;
}
.header__location{
position: absolute;
width: 94px;
height: 18px;
left: 595px;
top: 39px;
}

@media screen and (max-width: 1439px) {
.header__logo{
  width: 192px;
  height: 36px;
  left: 97px;
  top: 32px;
}
.header__location{
  width: 93.5px;
  height: 18.33px;
  left: 488px;
  top: 41px;
  }
}
@media screen and (max-width: 1023px) { 
  .header__logo{
    width: 239px;
    height: 33px;
    left: 118px;
    top: 32px;
  }
  .header__location{
    width: 93.5px;
    height: 18.33px;
    left: 642px;
    top: 38px;
    }
  }
  @media screen and (max-width: 767px) {
    .header__logo{
      width: 192px;
      height: 36px;
      left: 112px;
      top: 16px;
    }
    .header__location{
      width: 93.5px;
      height: 18.33px;
      left: 210px;
      top: 60px;
      }
  }