@font-face {
    font-family: 'Roboto-Bold';
    src: local('Roboto'), url(./../../fonts/Roboto-Bold.ttf) format('truetype');
  }
  @font-face {
    font-family: 'Roboto-Light';
    src: local('Roboto'), url(./../../fonts/Roboto-Light.ttf) format('truetype');
  }
  @font-face {
    font-family: 'Roboto-Medium';
    src: local('Roboto'), url(./../../fonts/Roboto-Medium.ttf) format('truetype');
  }
  
  
.app-wraper{
  position: relative;
  background-color: white;
  height: 100%;
  width: 100%;
}
  
.app-wraper__block{
display: flex;
position: relative;

}
.app-wraper__block__text1{
    position: absolute;
    width: 367px;
    height: 66px;
    left: 128px;
    top: 227px;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    font-family: Roboto-Bold;
    font-style: normal;
    font-weight: bold;
    font-size: 70px;
    line-height: 66px;
    color: #121212;
}
.app-wraper__block__text2{
position: absolute;
width: 447px;
height: 66px;
left: 128px;
top: 297px;

font-family: Roboto-Bold;
font-style: normal;
font-weight: bold;
font-size: 70px;
line-height: 66px;
color: #0EC261;
}
.app-wraper__block__text3{
position: absolute;
width: 560px;
height: 30px;
left: 128px;
top: 397px;

font-family: Roboto-Light;
font-style: normal;
font-weight: 300;
font-size: 26px;
line-height: 30px;
color: #999999;
}
.app-wraper__button{
position: absolute;
width: 250px;
height: 48px;
left: 128px;
top: 487px;

mix-blend-mode: normal;
background: linear-gradient(90deg, #0EC261 2.61%, #039F67 112.6%);
background-blend-mode: darken;
border-radius: 8px;
border: none;

font-family: Roboto-Medium;
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 21px;
text-align: center;

color: #FFFFFF;
}
.slider-block__text1{
  position: absolute;
  width: 421px;
  height: 47px;
  left: 96px;
  top: 237px;
  
  font-family: Roboto-Medium;
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 47px;
  color: #FFFFFF;
}
.slider-block__text2{
  position: absolute;
width: 495px;
height: 72px;
left: 96px;
top: 292px;

font-family: Roboto-Light;
font-style: normal;
font-weight: 300;
font-size: 24px;
line-height: 100%;
/* or 24px */


/* Gray_light */

color: #EEEEEE;
}
.slider-block__button{
position: absolute;
width: 164px;
height: 48px;
left: 96px;
top: 396px;
background: linear-gradient(90deg, #13493F 0%, #0C7B1B 100%);
border-radius: 4px;
border: none;

font-family: Roboto-Medium;
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 21px;
align-items: center;
text-align: center;
color: #EEEEEE;
}
.slider-block__button.color__slide2{
  background: linear-gradient(90deg, #132949 0%, #0C7B67 100%);
}
.slider-block__button.color__slide3{
  background: linear-gradient(90deg, #493013 0%, #7B0C3B 100%);
}
.slider-block__button.color__slide4{
  background: linear-gradient(90deg, #281349 0%, #720C7B 100%);
}

